* {
  box-sizing: border-box;
}

body {
  color: #333;
  background-color: #ffebef;
  margin: 0;
  padding: 20px;
  font-family: Poppins, sans-serif;
}

#habit-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  color: #c2185b;
  text-align: left;
  margin-left: 40px;
  font-family: Brush Script MT, cursive;
  font-size: 3.5rem;
  font-weight: 400;
}

nav {
  text-align: left;
  margin-bottom: 20px;
  padding-left: 40px;
}

nav a {
  color: #c2185b;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none;
  transition: background .3s;
}

nav a:hover {
  color: #7a0b3b;
  background-color: #f8bbd0;
}

#habitForm {
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
  display: flex;
}

#habitInput {
  border: 2px solid #c2185b;
  border-radius: 25px;
  flex: 1;
  padding: 12px 15px;
  font-size: 1rem;
  transition: border-color .3s, box-shadow .3s;
}

#habitInput:focus {
  border-color: #7a0b3b;
  outline: none;
  box-shadow: 0 0 10px #c2185b66;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #c2185b;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background .3s;
}

button:hover {
  background-color: #7a0b3b;
}

#habitList {
  max-width: 800px;
  margin: 20px auto;
  padding: 0;
  list-style: none;
}

#habitList li {
  background: #fff;
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 15px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 2px 6px #0000001a;
}

#habitList li:hover {
  transform: scale(1.02);
}

.habit-text {
  color: #7a0b3b;
  flex: 100%;
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: bold;
}

.streak {
  color: #555;
  font-size: 1rem;
}

.button-group {
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.edit-btn, .delete-btn, .save-btn, .checkin-btn {
  padding: 8px 15px;
  font-size: .9rem;
}

.checkin-btn {
  background-color: #4caf50;
  position: relative;
}

.checkin-btn:after {
  content: "Target: 7 days";
  color: #fff;
  opacity: .9;
  background: #4caf50;
  border-radius: 10px;
  padding: 2px 8px;
  font-size: .75rem;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

#chat-widget {
  background: #fff;
  border-radius: 10px;
  flex-direction: column;
  width: 300px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 2px 10px #0003;
}

#chat-widget.chat-widget-open {
  display: flex;
}

#chat-history {
  flex: 1;
  padding: 15px;
  overflow-y: auto;
}

#chat-input-area {
  border-top: 1px solid #ccc;
  padding: 10px;
  display: flex;
}

#chat-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex: 1;
  margin-right: 10px;
  padding: 8px;
}

#send-btn {
  color: #fff;
  cursor: pointer;
  background-color: #c2185b;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  transition: background .3s;
}

#send-btn:hover {
  background-color: #7a0b3b;
}
